import React, { useState } from "react"
import { PickerView } from "./picker"

interface MainViewProps {
    prefixes: string;
    suffixes: string;
}

export function MainView(props: MainViewProps): JSX.Element {
    const [prefix, setPrefix] = useState(" ");
    const [suffix, setSuffix] = useState(" ");

    return (
        <div className="main">
            <PickerView title="Prefix" uid="pid" path={props.prefixes} onNameChange={setPrefix}></PickerView>
            <PickerView title="Suffix" uid="sid" path={props.suffixes} onNameChange={setSuffix}></PickerView>
            <p>{prefix}{suffix}</p>
        </div>
    );
}
